<script setup lang="ts">
import { Heading } from '@/shared/ui/text/heading';
import { ref, computed, onMounted } from 'vue';
import { useStore } from 'vuex';
import axios from 'axios';
import { routes } from '@/app/router/routes';
import { Results } from '@/entities/results-before-after/model/results.types';
import { Button } from '@/shared/ui/button'
import { Icon } from '@/shared/ui/icons';

const store = useStore();
const results = ref<Results[]>([]);
const selectedSlug = ref('');
const selectedResultId = ref<string | null>(null);
const imageBefore = ref<File | null>(null);
const imageAfter = ref<File | null>(null);
const slug = ref('');
const isEditing = ref(false);
const isAdding = ref(false);
const currentImageBeforeUrl = ref<string | null>(null);
const currentImageAfterUrl = ref<string | null>(null);
const isLoading = ref(false);

const getAuthToken = () => {
    const token = localStorage.getItem('token');
    if (!token) {
        console.error('Токен отсутствует');
        return null;
    }
    return token;
};

const fetchResults = async () => {
    try {
        const response = await axios.get('http://5.35.90.116:8000/results');
        results.value = response.data;
    } catch (error) {
        console.error('Ошибка при получении результатов:', error);
    }
};

const serviceRoutes = computed(() => {
    const homeRoute = routes.find(route => route.path === '/');
    const servicesRoute = routes.find(route => route.path === '/services');

    const childServiceRoutes = servicesRoute?.children?.filter((childRoute) => childRoute.path !== '') || [];

    return [
        homeRoute && { name: homeRoute.name, path: homeRoute.path },
        ...childServiceRoutes.map(route => ({ name: route.name, path: `/services/${route.path}` }))
    ].filter(Boolean);
});

const filteredResults = computed(() => {
    if (!selectedSlug.value) {
        return results.value;
    }
    return results.value.filter(result => result.slug === selectedSlug.value);
});

const openEditForm = (result: Results) => {
    selectedResultId.value = result.id;
    currentImageBeforeUrl.value = result.imageBefore;
    currentImageAfterUrl.value = result.imageAfter;
    slug.value = result.slug;
    isEditing.value = true;
    isAdding.value = false;
};

const openAddForm = () => {
    selectedResultId.value = null;
    currentImageBeforeUrl.value = null;
    currentImageAfterUrl.value = null;
    slug.value = selectedSlug.value;
    isAdding.value = true;
    isEditing.value = false;
};

const onFileChange = (type: 'before' | 'after', event: Event) => {
    const input = event.target as HTMLInputElement;
    if (input && input.files && input.files[0]) {
        const file = input.files[0];

        const reader = new FileReader();
        reader.onload = (e) => {
            if (type === 'before') {
                imageBefore.value = file;
                currentImageBeforeUrl.value = e.target?.result as string;
            } else if (type === 'after') {
                imageAfter.value = file;
                currentImageAfterUrl.value = e.target?.result as string;
            }
        };
        reader.readAsDataURL(file);
    }
};

const removeImage = (type: 'before' | 'after') => {
    if (type === 'before') {
        currentImageBeforeUrl.value = null;
        imageBefore.value = null;
    } else if (type === 'after') {
        currentImageAfterUrl.value = null;
        imageAfter.value = null;
    }
};

const saveChanges = async () => {
    isLoading.value = true;
    const token = getAuthToken();
    if (!token) {
        console.error('Невозможно сохранить данные, так как отсутствует токен.');
        isLoading.value = false;
        return;
    }

    try {
        const formData = new FormData();
        if (slug.value) formData.append('slug', slug.value);
        if (imageBefore.value) formData.append('imageBefore', imageBefore.value);
        if (imageAfter.value) formData.append('imageAfter', imageAfter.value);

        if (isEditing.value && selectedResultId.value) {
            await axios.patch(`http://5.35.90.116:8000/results/${selectedResultId.value}`, formData, {
                headers: {
                    Authorization: `Bearer ${token}`,
                    'Content-Type': 'multipart/form-data'
                },
            });
        } else if (isAdding.value) {
            await axios.post('http://5.35.90.116:8000/results', formData, {
                headers: {
                    Authorization: `Bearer ${token}`,
                    'Content-Type': 'multipart/form-data'
                },
            });
        }
        fetchResults();
        closeForm();
    } catch (error) {
        console.error('Ошибка при сохранении:', error);
    } finally {
        isLoading.value = false;
    }
};

const closeForm = () => {
    selectedResultId.value = null;
    imageBefore.value = null;
    imageAfter.value = null;
    slug.value = '';
    isEditing.value = false;
    isAdding.value = false;
};
const deleteResult = async (resultId: string) => {
    const token = getAuthToken();
    if (!token) {
        console.error('Невозможно удалить результат, так как отсутствует токен.');
        return;
    }

    const confirmDelete = window.confirm('Вы уверены, что хотите удалить этот результат?');
    if (!confirmDelete) {
        return;
    }

    isLoading.value = true;
    try {
        await axios.delete(`http://5.35.90.116:8000/results/${resultId}`, {
            headers: {
                Authorization: `Bearer ${token}`,
            },
        });
        fetchResults();
    } catch (error) {
        console.error('Ошибка при удалении результата:', error);
    } finally {
        isLoading.value = false;
    }
};


onMounted(() => {
    fetchResults();
});
</script>
<template>
    <section class="admin-results">
        <div class="admin-header">
            <Heading tagName="h2" size="xl">результаты до / после</Heading>
            <select v-model="selectedSlug" class="slug-select">
                <option value="">Все страницы</option>
                <option v-for="route in serviceRoutes" :key="route?.path" :value="route?.path">{{ route?.name }}</option>
            </select>
        </div>
        <hr>
        <div class="results__content">
            <Button class="results__add-button" tagName="button" size="xs" color="green" @click="openAddForm"
                v-if="!isAdding">
                добавить новый результат
            </Button>
            <form v-if="isAdding" class="results__add-form">
                <div class="result__images">
                    <div class="image-before ">
                        <div v-if="currentImageBeforeUrl">
                            <img :src="currentImageBeforeUrl" alt="изображение до" />
                        </div>
                        <span @click="removeImage('before')" v-if="currentImageBeforeUrl">
                            <Icon type="cross" color="red" />
                        </span>
                        <input type="file" @change="onFileChange('before', $event)" v-if="!currentImageBeforeUrl" />
                    </div>

                    <div class="image-after">
                        <div v-if="currentImageAfterUrl">
                            <img :src="currentImageAfterUrl" alt="изображение после" />
                        </div>
                        <span @click="removeImage('after')" v-if="currentImageAfterUrl">
                            <Icon type="cross" color="red" />
                        </span>
                        <input type="file" @change="onFileChange('after', $event)" v-if="!currentImageAfterUrl" />
                    </div>
                </div>
                <div class="input-field">
                    <input type="text" v-model="slug" />
                    <label>путь к странице</label>
                </div>
                <div class="form__buttons">
                    <Button tagName="button" size="xs" color="green" @click="saveChanges">
                        <Paragraph tagName="span" color="dark">
                            Сохранить
                        </Paragraph>
                    </Button>
                    <Button tagName="button" size="xs" color="white" @click="closeForm">
                        <Paragraph tagName="span" color="dark">
                            Отмена
                        </Paragraph>
                    </Button>
                </div>
            </form>

            <div class="results-list">
                <div class="result-item" v-for="result in filteredResults" :key="result.id">
                    <div class="result__images">
                        <div class="image-before">
                            <img :src="result.imageBefore" alt="До">
                        </div>
                        <div class="image-after">
                            <img :src="result.imageAfter" alt="После">
                        </div>
                    </div>
                    <form v-if="isEditing && selectedResultId === result.id" class="results__edit-form">
                        <div class="result__images">
                            <div class="image-before ">
                                <div v-if="currentImageBeforeUrl">
                                    <img :src="currentImageBeforeUrl" alt="изображение до" />
                                </div>
                                <span @click="removeImage('before')" v-if="currentImageBeforeUrl">
                                    <Icon type="cross" color="red" />
                                </span>
                                <input type="file" @change="onFileChange('before', $event)" v-if="!currentImageBeforeUrl" />
                            </div>

                            <div class="image-after">
                                <div v-if="currentImageAfterUrl">
                                    <img :src="currentImageAfterUrl" alt="изображение после" />
                                </div>
                                <span @click="removeImage('after')" v-if="currentImageAfterUrl">
                                    <Icon type="cross" color="red" />
                                </span>
                                <input type="file" @change="onFileChange('after', $event)" v-if="!currentImageAfterUrl" />
                            </div>
                        </div>
                        <div class="input-field">
                            <input type="text" v-model="slug" />
                            <label>путь к странице:</label>
                        </div>
                        <div class="form__buttons">
                            <Button tagName="button" size="xs" color="green" @click="saveChanges">
                                <Paragraph tagName="span" color="dark">
                                    Сохранить
                                </Paragraph>
                            </Button>
                            <Button tagName="button" size="xs" color="white" n @click="closeForm">
                                <Paragraph tagName="span" color="dark">
                                    Отмена
                                </Paragraph>
                            </Button>
                        </div>
                    </form>
                    <div class="item__buttons">
                        <span @click="openEditForm(result)">
                            <Icon type="edit" color="dark" />
                        </span>
                        <span @click="deleteResult(result.id)">
                            <Icon type="delete" color="red" />
                        </span>
                    </div>
                </div>
            </div>
        </div>

        <div v-if="isLoading" class="loader">
            <div class="spinner"></div>
        </div>
    </section>
</template>

<style lang="scss" scoped>
@import './style.scss';
</style>